import React from 'react'
import img from './../sources/foulard.jpg'

export default function Header() {
    return (
        <div style={ headerStyle }>
            <h1 style={titleStyle} >Web punts</h1>
        </div>
    )
}

const headerStyle = {
    width: "100%",
    height: "100%",
    alignItems: "center",
    textAlign: 'center',
    padding: "10px",
    margin: "0",
    backgroundImage: `url(${img})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
}

const titleStyle = {
    color: 'white',
    textShadow: '2px 2px #000'
}